
.parallax-window {
  height: 100vh;
  background: transparent;
  background-image: url(assets/img/room.jpg);
}
.parallax-description{
color: whitesmoke;
font-size: 7rem;  
font-family: 'Open Sans Condensed', sans-serif;
text-align: center;
position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    
    @media screen and (max-width: 600px) {
      font-size: 3rem; 
      
    }
      
  }
